.menu{
  position: relative;
  width: 100%;
  min-height: 65px;
  background-color: #fff;
  color: $main-color;
  box-shadow: 0px 3px 0px 0px rgba(227,227,227,0.5);
}
.menu__items{
  display: flex;
  flex-direction: row;
}
.menu__item{
  margin-top: 5px;
  width: 33%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  &_icon{
    width: 25px;
    height: 25px;
    fill: $main-color;
    transition: all .3s  ease;
    &:hover{
      fill: lighten($main-color, 20%);
    }
  }
  &_lang{
    width: 25px;
    height: 25px;
    border: 2px solid $main-color;
    border-radius: 50%;
    color: $main-color;
    font-size: 10px;
    text-align: center;
    line-height: 20px;
    font-family: $ff-semi-bold;
    transition: all .3s ease;
    &:hover {
      color:  lighten($main-color, 20%);
      border-color: lighten($main-color, 20%);
    }
  }
  &_logout{
    position: relative;
  }
  &_username{
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: $main-color;
    border-radius: 2px;
    font-size: 14px;
    line-height: 21px;
    color: #fff;
    height: 25px;
    padding: 2px 5px;
  }
  .menu__logo {
    height: 60px;
  }
}
.menu__item:nth-child(odd){
  margin-top: 20px;
}
.search__form{
  position: absolute;
  display: flex;
  flex-direction: row;
  left: 55%;
  z-index: 5;
  top: 21%;
  width: 220px;
  transition: all 1s ease;
  background-color: #ffffff;
  .search__field{
    width: 60%;
    border: 2px solid $main-color;
    border-radius: 2px;
    font-family: $ff-regular;
    font-size: 14px;
    color: $dark-font;
    outline: none;
    padding-left: 3px;
  }
  .search__btn{
    width: 30%;
    background-color: #fff;
    border: none;
    outline: none;
    height: 40px;
    text-indent: -888px;
    background-image: url("../images/icons/search.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

#search__form{
  top: -300px;
}
#search__form:target{
  top: 22%;
}