.edit{
  flex: 1;
  &__wrapper{

  }
  .wrapper__left{
    width: 620px;
    margin-top: 15px;
    padding-bottom: 45px;
    border-radius: 5px;
    background-color: #fff;
    min-height: 360px;
  }
  &__header{
    height: 120px;
    background-color: $main-color;
    border-radius: 5px 5px 0 0;
    display: flex;
    position: relative;
    flex-direction: row;
    img{
      height: 135px;
      width: 135px;
      border-radius: 50%;
      border: 5px solid #fff;
      position: absolute;
      left: 20px;
      bottom: -60px;
    }
    .btn-edit__group{
      position: absolute;
      bottom: 20px;
      left: 180px;
      .btn-edit{
        background-color: transparent;
        border-radius: 5px;
        border: 2px solid #fff;
        font-size: 14px;
        font-family: $ff-semi-bold;
        color: #fff;
        padding: 5px 30px;
        margin-left: 20px;
        transition: all .3s ease;
        &:hover{
          color: $main-color;
          background-color: #ffffff;
        }
      }
    }
  }
  &__body{

  }
  &__form__wrapper {
    width: 215px;
    margin: 45px auto 0;
    .btn-save {
      font-size: 18px;
      width: 100%;
      &:hover{
        border: 2px solid $main-color;
      }
    }
  }
  &__field{
    margin-bottom: 55px;
    label {
      font-family: $ff-bold;
      font-size: 18px;
      color: $main-color;
      display: inline-block;
      margin-bottom: 20px;
    }
    input{
      width: 100%;
      height: 30px;
      border: none;
      outline: none;
      border-bottom: 2px solid $main-color;
      color: $main-color;
    }
  }
}