.recommended{
  margin-left: 20px;
  width: 330px;
  &__header{
    width: 100%;
    text-align: center;
    padding: 17px 0;
    background-color: $main-color;
    border-radius: 5px;
  }
  &__title{
    font-size: 15px;
    font-family: $ff-semi-bold;
    color: #fff;
  }
  &__item {
    width: 100%;
    margin-top: 10px;
    padding: 20px 15px;
    background-color: #fff;
    box-shadow: 0  3px 0 0  rgba(227, 227, 227, 0.9);
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    &_info{
      display: flex;
      flex-direction: row;
    }
    &_avatar{
      img{
        width: 55px;
        height: 55px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    &_name{
      padding-top: 10px;
      margin-left: 10px;
      font-family: $ff-semi-bold;
      font-size: 15px;
      color: $dark-font;
    }
    &_nickname{
      font-family: $ff-light;
      font-size: 12px;
      color: $dark-font;
    }
  }
  &__follow{

    padding: 12px 25px;
    line-height: 8px;
    margin-left: 20px;
    margin-top: 5px;

  }

}