@mixin center-xy() {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@mixin center-x() {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@mixin center-y() {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
@mixin placeholder($color, $size, $ff) {
  color: $color;
  font-family: $ff;
  font-size: $size;
}