.feed {
  flex: 1;
  &__wrapper {
    margin-top: 10px;
    width: 100%;
    display: flex;
    flex-direction: row;
    .feed__items{
      display: flex;
      flex-direction: column;

    }
  }
  &__item {
    width: 650px;

    background-color: #fff;
    border-radius: 5px;
    margin-bottom: 15px;
    padding-bottom: 10px;
    &_top{
      padding: 10px 20px;
      display: flex;
      flex-direction: row;
      line-height: 35px;
        img{
          width: 35px;
          height: 35px;
          border-radius: 50%;
      }

    }
    &_author {
      margin-left: 15px;
      color: $dark-font;
      font-family: $ff-semi-bold;
      font-size: 15px;
    }
    &_title {
      margin-left: 30px;
      font-family: $ff-semi-bold;
      font-size: 15px;
      color: $main-color;
    }
    &_body{
      img {
        width: 100%;
      }
    }
    &_bottom{
      padding: 10px 20px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      span {
        margin-left: 10px;
        margin-right: 30px;
        font-family: $ff-bold;
        color: $main-color;
        font-size: 15px;
      }
      .icon{
        vertical-align: middle;
      }
      .bottom__date{
        color: $dark-font;
        font-size: 14px;
        line-height: 24px;

        .date{
          font-family: $ff-semi-bold;
        }
      }
    }
  }
}
.liked{
  stroke-width: 55;
  fill: red;
}