.create{
  flex: 1;
  .create__wrapper{
    padding-top: 55px;
    padding-left: 80px;
    color: $main-color;
  }
  .wrapper__left{
    width: 620px;
    margin-top: 15px;
    padding-bottom: 45px;
    border-radius: 5px;
    background-color: #fff;
    min-height: 360px;
  }
  .file__input {
    overflow: hidden;
    position: relative;
    background-color: transparent;
    padding: 5px 60px;
    border: 2px solid $main-color;
    color: $main-color;
    border-radius: 2px;
    font-family: $ff-semi-bold;
    &:hover{
      background-color: $main-color;
      color: #ffffff;
    }
  }

  .file__input [type=file] {
   position: absolute;
    left: -8888px;

  }
  .form__field{
    &:nth-child(2){
      margin-top: 40px;
      label{
        display: inline-block;
        margin-bottom: 20px;
        font-family: $ff-semi-bold;

      }
      input{
        width: 300px;
        height: 30px;
        border: none;
        border-bottom: 2px solid $main-color;
        outline: none;
      }
    }
    p{
      margin-bottom: 30px;
      font-family: $ff-semi-bold;
    }
  }
  .btn-create{
    font-size: 18px;
    font-family: $ff-semi-bold;
    border-radius: 2px;
    padding: 5px 57px;
    margin-bottom: 90px;
    margin-top: 90px;
    outline: none;
  }
}