.main {

  flex: 1;
  &__wrapper{
    margin: 10px 30px 0px 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__user{
    width: 225px;
    height: 550px;
    background-color: #ffffff;
    border-radius: 5px;
    &_avatar{
      position: relative;
      width: 100%;
      height: 120px;
      background-color: $main-color;
      border-radius: 5px 5px 0 0;
      img{
        position: absolute;
        bottom: -55px;
        left: 45px;
        width: 135px;
        height: 130px;
        border-radius: 50%;
        border: 5px #ffffff solid;
      }
    }
    &_description{
      position: relative;
      margin-top: 80px;
      text-align: center;
      .user__name{
        font-size: 18px;
        font-family: $ff-bold;
        color: $dark-font;
      }
      .user__nickname{
         font-family: $ff-regular;
         font-size: 14px;
         color: lighten($dark-font, 10%);
         margin-bottom: 10px;
     }
      .user__descr{
        padding: 0 15px;
        font-size: 12px;
        color: lighten($dark-font, 10%);
      }
      .user__settings{
        position: absolute;
        right: 30px;
        top: 0;
        &_icon{
          width: 25px;
          height: 25px;
          fill: $main-color;
        }
      }
    }
    &_follow{
      text-align: center;
      margin-top: 15px;
      .btn__follow{
        display: inline-block;
        color: #fff;
        background-color: $green;
        width: 185px;
        height: 45px;
        border-radius: 5px;
        font-size: 16px;
        font-family: $ff-bold;
        line-height: 44px;
        transition: all .2s ease ;
        &:hover{
          background-color: darken($green, 20%);
        }
      }
    }
    &_info{
      text-align: center;
      height: 30%;
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .count{
        font-family: $ff-bold;
        font-size: 16px;
        color: $main-color;
      }
      .info_title {
        color: $dark-font;
        font-size: 14px;
        font-family: $ff-semi-bold;
      }
      a:hover{
        color: lighten($dark-font, 30%);
      }
    }
  }
  &__posts{
    .posts_wrapper{
      width: 700px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .posts__row{
        margin-bottom: 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
      }
    }
    .post_item {
      position: relative;
      img{
        width: 220px;
        height: 220px;
      }
      .post__item__overlay{
        position: absolute;
        background-color: #000;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity .4s ease;
        &:hover{
          opacity: .7;
        }
        .overlay__content{
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          @include center-xy;
          color: #ffffff;
          font-family: $ff-bold;
          .post_comments{
            margin-left: 10px;
            span{
              position: absolute;
              top: 5px;
              margin-left: 4px;
            }
          }
          .post_likes{
            margin-right: 25px;
            span{
              position: absolute;
              top: 5px;
              margin-left: 4px;
            }
          }
          .overlay_icon{
            fill: #fff;
            width: 25px;
            height: 25px;
          }
        }
      }
    }
  }
}