.modal{
  @include center-xy;
  width: 460px;
  height: 480px;
  background-color: #fff;
  border-radius: 5px;
  padding: 0 0 15px 0;
  &__header{
    background-color: $main-color;
    border-radius: 5px 5px 0 0;
    color: #ffffff;
    text-align: center;
    font-size: 16px;
    font-family: $ff-semi-bold;
    padding: 22px 0;
    position: relative;
    &__close{
      display: inline-block;
      position: absolute;
      right: 20px;
      top: 50%;
      width: 30px;
      height: 30px;
      background-color: transparent;
      transition: all .3s ease;
      .close__bar{
        height: 2px;
        width: 25px;
        background-color: #ffffff;
        transform: rotate(45deg);
        &:after{
          display: block;
          content: "";
          height: 2px;
          width: 25px;
          background-color: inherit;
          transform: rotate(90deg);
        }
        &:hover{
         opacity: .6;
        }
      }
    }

  }
  &__body{
    padding: 10px 62px;
    height: 380px;
    overflow: auto;

    .recommended__item{
      box-shadow: none;
      margin: 0;
      border-radius: 0;
      border-bottom: 1px solid $main-color;
    }
  }
}

#followers-modal{
  top: -500px;
  transition: top 1s ease;
}
#followers-modal:target{
  top: 400px;
}
#following-modal{
  top: -500px;
  transition: top 1s ease;
}
#following-modal:target{
  top: 400px;
}