.notfound{
  flex: 1;
  &__wrapper{
    margin: 200px auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .notfound__left{
    position: relative;
    .back__link{
      color: $main-color;
      font-family: $ff-bold;
      font-size: 18px;
      &:hover{
        text-decoration: underline;
      }
    }
    .sign{
        position: absolute;
        bottom: -100px;
        right: -100px;
        color: $main-color;
        font-size: 180px;
        font-family: $ff-bold;

      }
  }
  &__header{
    color: $main-color;
    font-size: 60px;
    font-family: $ff-bold;
  }

}