.login{
  flex: 1;
  &__wrapper{
    width: 460px;
    background-color: #fff;
    border-radius: 5px;
    margin: 40px auto;
    min-height: 400px;
    padding: 38px 0px 0;
    text-align: center;
  }
  &__logo{
    margin-bottom: 30px;
    img{
      width: 150px;
    }
  }
  &__form{
    padding: 0 60px;
    form{
      display: flex;
      flex-direction: column;
      .btn-signin{
        font-size: 15px;
        font-family: $ff-semi-bold;
      }
      span {
        padding: 10px;
        font-size: 12px;
        font-family: $ff-semi-bold;
        color: lighten($dark-font, 20%);
      }
      .btn-fb{
        background-color: $blue;
        border: none;
        &:hover{
          background-color: lighten($blue, 20%);
        }
        img{
          vertical-align: middle;
        }
        span{
          color: #ffffff;
        }
      }
    }
    .form__field {
      height: 40px;
      border: 1px solid $main-color;
      border-radius: 2px;
      outline: none;
      margin-bottom: 15px;
      padding: 0 25px;
      @include placeholder(lighten($dark-font, 20%), 14px, $ff-regular)

      &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        @include placeholder(lighten($dark-font, 20%), 14px, $ff-regular)
      }
      &::-moz-placeholder { /* Firefox 19+ */
        @include placeholder(lighten($dark-font, 20%), 14px, $ff-regular)
      }
      &:-ms-input-placeholder { /* IE 10+ */
        @include placeholder(lighten($dark-font, 20%), 14px, $ff-regular)
      }
      &:-moz-placeholder { /* Firefox 18- */
        @include placeholder(lighten($dark-font, 20%), 14px, $ff-regular)
      }
    }
    .email__field{
      background-image: url("../images/name_ico.png");
      background-repeat: no-repeat;
      background-position: 2% 50%;
    }
    .password__field{
      background-image: url("../images/lock_ico.png");
      background-repeat: no-repeat;
      background-position: 2% 50%;
    }
  }
  &__bottom{
    width: 100%;
    border-top: 1px solid $gray;
    border-radius: 0 0 5px 5px;
    margin-top: 50px;
    background-color: #f9fbfd;
    padding: 20px 60px ;
    font-size: 12px;
    font-family: $ff-semi-bold;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &_link{
      color: $main-color;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}