.single {
  flex: 1;
  &__wrapper {
    width: 100%;
    background-color: #fff;
    min-height: 620px;
    border-radius: 5px;
    margin-top: 25px;
    margin-bottom: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .single__comment_form {
      padding: 10px 20px;

      .leave__comment {
        width: 85%;
        border: none;
        color: $main-color;
        outline: none;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: $main-color;
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: $main-color;
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: $main-color;
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: $main-color;
        }
      }
      .send__comment {
        width: 50px;
        height: 50px;
        background-image: url("../images/icons/bubble.svg");
        fill: #fff;
        background-position: center;
        background-repeat: no-repeat;
        border: none;
        outline: none;
        background-color: #fff;
        text-indent: -999px;
        margin-left: 40px;
      }
    }
  }
  .single__info {
    height: 600px;
    overflow-y: scroll;
    padding: 10px 15px 10px 35px;
    display: flex;
    flex-direction: column;
    &__author {
      margin-bottom: 10px;
      .author_avatar {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        vertical-align: middle;
      }
      .author__nickname {
        margin-left: 20px;
        font-family: $ff-semi-bold;
        font-size: 15px;
        color: $dark-font;
      }
    }
    &__title {
      font-family: $ff-semi-bold;
      font-size: 15px;
      color: $main-color;
      margin-bottom: 20px;
    }
    &__actvity {
      margin-bottom: 20px;
      padding-bottom: 10px;
      border-bottom: 2px solid lighten($dark-font, 30%);
      span {
        margin-left: 10px;
        margin-right: 30px;
        font-family: $ff-bold;
        color: $main-color;
        font-size: 15px;
      }
      .icon{
        vertical-align: middle;
      }
    }
    &__comment{
      color: $dark-font;
      margin-bottom: 20px;
      .comment__author{
        font-family: $ff-semi-bold;
        font-size: 15px;
        margin-bottom: 5px;

      }
      .comment__body {
        font-family: $ff-light;
        font-size: 14px;
      }
    }
  }
}
